<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}" style="padding-top: 1rem;">
        <!-- 头部区域 -->
      <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
        <nav-head :title="title" ></nav-head>
      </div>
      <!-- 下拉框 -->
      <div class="pulldown-box">
          <van-dropdown-menu>
            <van-dropdown-item  @change="usrChange" v-model="value_a" :options="option_a" />
          </van-dropdown-menu>
      </div>
      <!-- 转换区域 -->
      <div class="zhuanhuan-box">
          <div class="left-box">
              <span class="cong">从</span>
              <span class="line"></span>
              <span class="dao">到</span>
          </div>
          <div class="center-box">
              <div class="pulldown-a">
                  <van-dropdown-menu>
                    <van-dropdown-item v-model="value_b" :options="option_b" @change="getOption_c" />
                  </van-dropdown-menu>
              </div>
              <div class="pulldown-b">
                  <van-dropdown-menu>
                    <van-dropdown-item v-model="value_c" :options="option_c" />
                  </van-dropdown-menu>
              </div>
          </div>
          <div class="right-box">
              <img src="../../assets/image/huazhuan/xunhuan.png" alt="" @click="change_up_down">
          </div>
      </div>
      <!-- 划转数量 区域 -->
      <div class="huazhuan-num-box">
          <div class="huazhuan-num-a">
           <span>划转数量 : </span>
           <input type="text" v-model="transferData.num" placeholder="请输入最低100的划转数量">
          </div>
          <div class="huazhuan-num-b">总额：<span class="num-b">{{total}} {{coin}}</span>
          </div>
          <div class="huazhuan-num-c" v-if="spanActive <2">可用余额：<span class="num-b">{{available}} {{coin}}</span></div>
      </div>
      <!-- 按钮 -->
      <div class="bottom-btn" @click="userBut()">
          确认划转
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { transfer } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import NavHead from '@/components/head.vue'
import { DropdownMenu, DropdownItem, Toast } from 'vant'

Vue.use(DropdownMenu).use(DropdownItem).use(Toast)
// Vue.
export default {
  components: { NavHead },
  data () {
    return {
      title: '划转',
      curHeight: '',
      value_a: 1,
      value_b: 1,
      value_c: 2,
      bb_item: '币币',
      gg_item: '杠杆',
      hy_item: '合约',
      item_a: '杠杆',
      item_b: '合约',
      item_b_v: 2,
      item_c_v: 2,
      option_a: [
        { text: 'BTC', value: 1 },
        { text: 'ETH', value: 2 },
        { text: 'USDT', value: 3 },
        { text: 'LBTH', value: 4 }
      ],
      option_b: [
        { text: '币币账户', value: 1 },
        { text: '杠杆账户', value: 2 },
        { text: '合约账户', value: 3 }
      ],
      option_c: [
        { text: '杠杆账户', value: 2 },
        { text: '合约账户', value: 3 }
      ],
      transferData: {
        u_id: '', // 用户ID
        xtype: '', // 划转类型（1币币转合约 2合约转币币 3币币转杠杆 4杠杆转币币）
        cointype: '', // 划转币种（btc usdt lbth eth）
        num: '', // 数量
        start: '', // 出仓类型（币币账户 合约账户 杠杆账户）
        end: '' // 进仓类型 （币币账户 合约账户 杠杆账户）
      },
      coin: '', // 展示的币种
      spanActive: '', // 判断循环数据
      total: '0', // 总数
      available: '0' // 可用
    }
  },
  created () {
    this.beforeMount()
    this.transferData.u_id = sessionStorage.getItem('u_id')
    var types = this.$route.query.types
    this.spanActive = this.$route.query.spanActive
    console.log(types, 'var types = this.$route.query.types')
    //
    if (this.spanActive === 1) {
      this.option_a = []
      for (var i = 0; i < 4; i++) {
        var obj = {}
        if (i === 0) {
          obj.text = 'BTC'
          obj.total = types.btc_num
          obj.available = types.btc_available_balance
          obj.value = i + 1
          this.option_a.push(obj)
        } else if (i === 1) {
          obj.text = 'ETH'
          obj.total = types.eth_num
          obj.available = types.eth_available_balance
          obj.value = i + 1
          this.option_a.push(obj)
        } else if (i === 2) {
          obj.text = 'USDT'
          obj.total = types.usdt_num
          obj.available = types.usdt_available_balance
          obj.value = i + 1
          this.option_a.push(obj)
        } else if (i === 3) {
          obj.text = 'LBTH'
          obj.total = types.lbth_num
          obj.available = types.lbth_available_balance
          obj.value = i + 1
          this.option_a.push(obj)
        }
      }
    }
    if (this.spanActive === 2 || this.spanActive === 3) {
      console.log('22222', types.btc)
      this.option_a = []
      for (var ii = 0; ii < 4; ii++) {
        var obj2 = {}
        if (ii === 0) {
          obj2.text = 'BTC'
          obj2.total = types.btc
          // obj2.available = types.btc_available_balance
          obj2.value = ii + 1
          this.option_a.push(obj2)
        } else if (ii === 1) {
          obj2.text = 'ETH'
          obj2.total = types.eth
          // obj2.available = types.eth_available_balance
          obj2.value = ii + 1
          this.option_a.push(obj2)
        } else if (ii === 2) {
          obj2.text = 'USDT'
          obj2.total = types.usdt
          // obj2.available = types.usdt_available_balance
          obj2.value = ii + 1
          this.option_a.push(obj2)
        } else if (ii === 3) {
          obj2.text = 'LBTH'
          obj2.total = types.lbth
          // obj2.available = types.lbth_available_balance
          obj2.value = ii + 1
          this.option_a.push(obj2)
        }
      }
      console.log(this.option_a, 'this.option_a')
    }
    if (this.spanActive === 4) {
      console.log('4444', types.btc)
      this.option_a = []
      for (var iii = 0; iii < 4; iii++) {
        var obj4 = {}
        if (iii === 0) {
          obj4.text = 'USD'
          obj4.total = types.my_cash
          // obj2.available = types.btc_available_balance
          obj4.value = iii + 1
          this.option_a.push(obj4)
        } else if (iii === 1) {
          obj4.text = 'CNY'
          obj4.total = types.reb_cash
          // obj2.available = types.eth_available_balance
          obj4.value = iii + 1
          this.option_a.push(obj4)
        }
      }
      console.log(this.option_a, 'this.option_a')
    }
    var text = this.option_a[0].text
    this.coin = this.option_a[0].text
    this.total = this.option_a[0].total
    this.available = this.option_a[0].available
    this.transferData.cointype = text.toLowerCase()
    // ------------------
    this.transferData.start = this.option_b[0].text
    this.transferData.end = this.option_c[0].text
  },
  methods: {
    userBut () {
      console.log(this.transferData, 'this.transferData')
      if (this.transferData.start === '币币账户' && this.transferData.end === '杠杆账户') {
        this.transferData.xtype = 3
      } else if (this.transferData.start === '币币账户' && this.transferData.end === '合约账户') {
        this.transferData.xtype = 1
      } else if (this.transferData.start === '杠杆账户' && this.transferData.end === '币币账户') {
        this.transferData.xtype = 4
      } else if (this.transferData.start === '合约账户' && this.transferData.end === '币币账户') {
        this.transferData.xtype = 2
      }
      this.transferFun()
    },
    usrChange (value) {
      console.log('type', value)
      var that = this
      for (const [i, v] of that.option_a.entries()) {
        if (v.value === value) {
          console.log('等于了', i, v, v.text)
          that.coin = v.text
          that.total = v.total
          that.available = v.available
          that.transferData.cointype = v.text.toLowerCase()
        }
      }
    },
    transferFun () {
      var that = this
      var data = objKeySort(this.transferData)
      var token = sgin(data)
      this.transferData.token = token
      transfer(this.transferData).then(res => {
        console.log(res, that, '交易成功')
        if (res.code === 0) {
          that.$toast({ message: '划转成功', duration: 1500 })
          // that.transferData.num = ''
          // that.transferData.paypass = ''
          delete this.transferData.token
        }
        // this.datas = res.data
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        // that.transferData.num = ''
        // that.transferData.paypass = ''
        delete this.transferData.token
      })
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
    },
    //   根据第一栏  判断第二栏   1-bb   2-gg 2-hy
    getOption_c (value) {
      console.log(value)
      var that = this
      //
      for (const [i, v] of that.option_b.entries()) {
        if (v.value === value) {
          console.log('等于了', i, v, v)
          that.transferData.start = v.text
        }
      }
      if (value === 1) {
        this.value_c = 2
        this.option_c = [
          { text: '杠杆账户', value: 2 },
          { text: '合约账户', value: 3 }
        ]
        that.transferData.end = that.option_c[0].text
        console.log('11111111111')
      }
      if (value === 2) {
        console.log('222222222222')
        this.value_c = 1
        this.option_c = [
          { text: '币币账户', value: 1 }
        ]
        that.transferData.end = that.option_c[0].text
      }
      if (value === 3) {
        console.log('33333333333')
        this.value_c = 1
        this.option_c = [
          { text: '币币账户', value: 1 }
        ]
        that.transferData.end = that.option_c[0].text
      }
    },
    // 上下  换
    change_up_down () {
      // var temp = this.value_b
      // this.value_b = this.value_c
      // this.value_c = temp
      var b = this.option_b
      var c = this.option_c
      this.option_b = c
      this.option_c = b
      console.log('this.value_b', this.value_b)
      console.log('this.value_b', this.value_b)
      console.log(b)
      console.log(c)
      this.value_b = this.option_b[0].value
      this.value_c = this.option_c[0].value
      // this.option_b = [
      //   { text: '币币账户', value: 1 },
      //   { text: '杠杆账户', value: 2 },
      //   { text: '合约账户', value: 3 }
      // ]
      // this.option_c = [
      //   { text: '币币账户', value: 1 },
      //   { text: '杠杆账户', value: 2 },
      //   { text: '合约账户', value: 3 }
      // ]
      this.transferData.start = this.option_b[0].text
      this.transferData.end = this.option_c[0].text
    }
  },
  mounted: function () {
    // console.log('ooooo')
    // this.getOption_c()
  }
}
</script>
